import sqlite3InitModule from '@sqlite.org/sqlite-wasm';
console.log('This is worker.js');

const log = (...args) => console.log(...args);
const error = (...args) => console.error(...args);

const start = function (sqlite3) {

  const readDatabase = (arrayBuffer) => {
    let bytes = new Uint8Array(arrayBuffer);
    const p = sqlite3.wasm.allocFromTypedArray(bytes);
    const db = new sqlite3.oo1.DB();
    let rc = sqlite3.capi.sqlite3_deserialize(
      db.pointer,
      "main",
      p,
      bytes.length,
      bytes.length,
      sqlite3.capi.SQLITE_DESERIALIZE_FREEONCLOSE |
        sqlite3.capi.SQLITE_DESERIALIZE_RESIZEABLE
    );
    return db;
  };

  // readAsArrayBuffer('/ssdb.sqlite3').then((r) => {
  //   const db = readDatabase(r);
  // }).catch((e) => {
  //   console.log(e);
  // });

  (async function () {
    log('Running SQLite3 version', sqlite3.version.libVersion);
    // const db = new sqlite3.oo1.DB('/ssdb.sqlite3', 'ct');
    
    const dataPromise = await fetch('/ssdb.sqlite3').then(res => res.arrayBuffer());
    const u8array = new Uint8Array(dataPromise);
    const db = readDatabase(u8array);

    log("transient db =", db.filename);

    try {
      // log('Creating a table...');
      // db.exec('CREATE TABLE IF NOT EXISTS t(a,b)');
      // log('Insert some data using exec()...');
      // for (let i = 20; i <= 25; ++i) {
      //   db.exec({
      //     sql: 'INSERT INTO t(a,b) VALUES (?,?)',
      //     bind: [i, i * 2],
      //   });
      // }
      // log('Query data with exec()...');
      // db.exec({
      //   sql: 'SELECT a FROM t ORDER BY a LIMIT 3',
      //   callback: (row) => {
      //     log(row);
      //   },
      // });

      db.exec({
        sql: "select * from sqlite_master",
        callback: (row) => {
          log(row);
        },
      });

      db.exec({
        sql: 'SELECT * FROM Test ORDER BY id LIMIT 3',
        callback: (row) => {
          log(row);
        },
      });

    } finally {
      db.close();
    }
  })();
};

log('Loading and initializing SQLite3 module...');
sqlite3InitModule({
  print: log,
  printErr: error,
}).then((sqlite3) => {
  log('Done initializing. Running demo...');
  try {
    start(sqlite3);
  } catch (err) {
    error(err.name, err.message);
  }
});
